import { cubicInOut } from 'eases-jsnext';

function draw(node, params) {
  var len = node.getTotalLength();
  return {
    delay: params.delay,
    duration: params.duration || 800,
    easing: params.easing || cubicInOut,
    css: function
    /*Babel»*/
    css(t, u)
    /*Babel»*/
    {
      return "stroke-dasharray: " + t * len + " " + u * len;
    }
  };
}

export default draw;